:root {
  --font-primary: "Raleway", sans-serif;
}

:root {
  --color-bar: hsl(6, 100%, 80%), hsl(335, 100%, 65%);
  --color-bg-cover: hsl(228, 56%, 26%);
  --color-bg-icons: hsl(229, 57%, 11%);
  --color-icons: hsl(228, 55%, 62%);
  --color-text-bold: hsl(243, 100%, 93%);
  --color-text: hsl(229, 7%, 55%);
}

html {
  font-size: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-size: 14px;
  background: url(../images/bg-mobile.png) no-repeat;
  background-size: cover;
}
@media (min-width: 48em) {
  body {
    background: #0c122c url("../images/bg-desktop.png") no-repeat;
    background-position: bottom;
    background-size: contain;
  }
}

.cover {
  background-color: var(--color-bg-cover);
  border-radius: 0.5rem;
  padding: 2.5rem;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.3);
}
@media (max-width: 21.875em) {
  .cover {
    padding: 1.875rem;
  }
}

.bold {
  color: var(--color-text-bold);
  font-weight: 700;
}

a {
  text-decoration: none;
}

.container {
  max-width: 25rem;
  width: 90vw;
  gap: 1.5rem;
}
@media (min-width: 48em) {
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 56.25rem;
  }
}

.cover-left {
  border-top-right-radius: 6.25rem;
}
@media (min-width: 48em) {
  .cover-left {
    padding-right: 4rem;
  }
}
@media (min-width: 64em) {
  .cover-left {
    padding-right: 7rem;
  }
}
.cover-left__files {
  display: flex;
  align-items: center;
  justify-content: initial;
  gap: 1rem;
  margin-top: 2rem;
}
.cover-left__icon {
  font-size: 2rem;
  padding: 0.5rem;
  color: #697ed3;
  background-color: var(--color-bg-icons);
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background 0.7s linear, color 0.5s linear;
}
.cover-left__icon:hover {
  color: var(--color-bg-icons);
  background-color: var(--color-icons);
}

.cover-right {
  position: relative;
  margin-top: 1rem;
  flex: 1;
  align-self: flex-end;
}
.cover-right__title {
  text-align: center;
  color: var(--color-text);
}
@media (min-width: 48em) {
  .cover-right__title {
    text-align: initial;
  }
}
.cover-right__bar {
  position: relative;
  height: 1.25rem;
  background-color: #151e47;
  border: 3px solid transparent;
  border-radius: 1.875rem;
  margin: 1rem 0;
}
.cover-right__progress {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 75%;
  background: linear-gradient(to right, var(--color-bar));
  border-radius: 1.875rem;
}
.cover-right__progress::before {
  content: "";
  display: block;
  position: absolute;
  top: 0.125rem;
  bottom: 0.125rem;
  right: 0.1875rem;
  width: 0.625rem;
  background-color: var(--color-text-bold);
  border-radius: 100%;
}
.cover-right__storage {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.625rem;
}
@media (min-width: 48em) {
  .cover-right__storage {
    margin-bottom: 0;
  }
}
.cover-right__pop-up {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background-color: #fff;
  border-radius: 0.5rem;
  height: 4.0625rem;
  width: 10rem;
  position: absolute;
  bottom: -40%;
  right: 50%;
  transform: translate(50%, -50%);
  cursor: pointer;
}
@media (min-width: 48em) {
  .cover-right__pop-up {
    transform: initial;
    top: -2.5rem;
    right: 2.3125rem;
    border-bottom-right-radius: 0;
  }
  .cover-right__pop-up::before {
    content: "";
    display: block;
    background-color: #fff;
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translateY(70%);
    -webkit-clip-path: polygon(100% 0, 0 0, 100% 100%);
    clip-path: polygon(100% 0, 0 0, 100% 100%);
  }
}
.cover-right__gigabyte {
  font-size: 2.5rem;
  font-weight: 700;
}
.cover-right__gigabyte > span {
  display: inline-block;
  font-size: 0.75rem;
  color: var(--color-text);
  text-transform: uppercase;
  transform: translateY(-5px);
}